<template>
  <div id="legal-record" :class="skins=='days'?'whites':'whites balck'">
    <div class="title bgf8">{{$t('fat.orderLog')}}</div>
    <div class="filter-box">
      <!-- <div>
        <span>{{$t('fat.tdType')}}：</span>
        <span :class="{'select':filterPms.type == 'sell'}" @click="filterPms.type = 'sell';getList()">{{$t('td.buy')}}</span>
        <span :class="{'select':filterPms.type  == 'buy'}" @click="filterPms.type  = 'buy';getList()">{{$t('td.sell')}}</span>
      </div> -->
      <div>
        <span>{{$t('fat.odStatus')}}：</span>
        <span :class="{'select':filterPms.isSure == 0}" @click="filterPms.isSure = 0;getList()">{{$t('td.nofinish')}}</span>
        <span :class="{'select':filterPms.isSure == 1}" @click="filterPms.isSure = 1;getList()">{{$t('td.finished')}}</span>
        <span :class="{'select':filterPms.isSure == 2}" @click="filterPms.isSure = 2;getList()">{{$t('td.ceiled')}}</span>
        <span :class="{'select':filterPms.isSure == 3}" @click="filterPms.isSure = 3;getList()">{{$t('td.payed')}}</span>
      </div>
    </div>
    
    <ul class="bgf8">
      <li v-for="(item,index) in list" :key="index" class="bod_bc bdb pdtb5">
        <div class="flex li-t pdtb5">
          <div>
            <span v-if="item.type == 'sell'">{{$t('td.buy')}}</span>
            <span v-else>{{$t('td.sell')}}</span>
            <span>{{item.currency_name}}</span>
          </div>
          <div class="status">
            <router-link :to="{path:'/shopLegalPayDetail',query:{id:item.id}}" v-if="item.is_sure == 0">{{$t('td.nofinish')}} ></router-link>
            <router-link :to="{path:'/shopLegalPayDetail',query:{id:item.id}}" v-else-if="item.is_sure == 1">{{$t('td.finished')}} ></router-link>
            <router-link :to="{path:'/shopLegalPayDetail',query:{id:item.id}}" v-else-if="item.is_sure == 2">{{$t('td.ceiled')}} ></router-link>
            <router-link :to="{path:'/shopLegalPayDetail',query:{id:item.id}}" v-else>{{$t('td.payed')}} ></router-link>
          </div>
        </div>
        <div class="flex li-b">
          <div>
            <div class="tc ft14">{{$t('td.time')}}</div>
            <div class="tc">{{item.create_time}}</div>
          </div>
          <div>
            <div class="tc ft14">{{$t('td.num')}}（{{item.currency_name}})</div>
            <div class="tc">{{item.number || '0.000' | numFilters(3)}}</div>
          </div>
          <div>
            <div class="tc ft14">{{$t('td.tradeTotal')}}（CNY）</div>
            <div class="tc">{{item.deal_money || '0.000' | numFilters(3)}}</div>
          </div>
        </div>
      </li>
      
    </ul>
     <!-- 分页 -->
      <el-pagination
        layout="prev, pager, next"
        v-if="total > 10"
        :total="total"
        @current-change="handleCurrentChange"
      ></el-pagination>
    <!-- <div class="more" @click="getList(true)" v-if="list.length">加载更多</div>
   
    <div v-else class="nomore">{{$t('td.nomore')}}</div> -->
     
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      filterPms: { type: "none", id: '', page: 1, isSure: "none" },
      total: 0,
      skins: localStorage.getItem("skin") || "days"
    };
  },
  created() {
    var token = window.localStorage.getItem("token") || "";
    if (token) {
      this.token = token;
      this.filterPms.id = this.$route.query.id || "";
      this.getList();
    }
  },
  methods: {
    // 分页
    handleCurrentChange(val) {
      var that = this;
      that.filterPms.page = val;
      that.getList();
    },
    getList() {
      var pms = {};
      this.list = [];
      pms.id = this.filterPms.id;
    //  pms.id = '55'
      pms.page = this.filterPms.page;
      if (this.filterPms.type != "none") {
        pms.type = this.filterPms.type;
      }
      if (this.filterPms.isSure != "none") {
        pms["is_sure"] = this.filterPms.isSure;
      }
      var i = layer.load();
      this.$http({
        url: "/api/legal_send_deal_list",
        params: pms,
        headers: { Authorization: this.token }
      }).then(res => {
        layer.close(i);
        if (res.data.type == "ok") {
          var msg = res.data.message;
            if (msg.data.length) {
              this.list = msg.data;
              this.total=msg.total;
            } else {
              layer.msg(this.$t('td.nomore'));
            }
        }
      });
    }
  }
};
</script>

<style lang='scss' scoped>
.whites {
  width: 1200px;
  margin: 66px auto 50px;
  background-color: #fff;
  min-height: 630px;
  padding-bottom:30px;
  position: relative;
  top: 18px;
  > .title {
    margin-bottom: 30px;
    padding: 0 30px;
    line-height: 50px;
    font-size: 20px;
    // background: #f8f8f8;
  }
  > .filter-box {
    line-height: 30px;
    // background: #f8f8f8;
    span {
      margin-left: 16px;
      cursor: pointer;
      padding-bottom: 3px;
    }
    // span:nth-child(n + 2) {
    //   font-weight: 600;
    // }
    .select {
      border-bottom: 2px solid #f0b90b;
    }
  }
  > ul {
    padding: 10px 30px;
    // background: #f8f8f8;
    li {
      > div {
        justify-content: space-between;
        line-height: 30px;
      }
    }
  }

  > .more {
    text-align: center;
    padding: 20px;
    // background: #f8f8f8;
    cursor: pointer;
  }
  >.nomore{
    padding: 16px;
    text-align: center;
  }
}
.log_wrap{
  overflow: auto;
}
.el-pagination{
  text-align: center;
  margin-top: 30px;
}
.el-pagination .btn-prev,.el-pagination .btn-next{
  display: none;
}
.el-pager .active{
  color: #f0b90b;
}
.balck{
  background-color: #1e2b34;
  color: #fff;
}
.balck .bdb{
  border-bottom: 1px solid #000;
}
</style>



